IG$.initDaD = function(el, panel) {
	var owner = panel,
		grid = panel;
	
	this.panel = panel;
	this.el = el;
	
	this.dropZone = $s.create($s.dropzone, el, {
		ddGroup: '_I$RD_G_',
		
		nodeouttimer: -1,
		
		getTargetFromEvent: function(e) {
			var px = e.browserEvent.pageX,
				py = e.browserEvent.pageY,
				renderer = grid.getRendererByPoint(px, py);
			
			if (renderer == null)
			{
				renderer = el;
			}
			
			return renderer;
		},
		
		notifyOut : function(dd, e, data){
			if(this.lastOverNode){
				this.onNodeOut(this.lastOverNode, dd, e, data);
				this.lastOverNode = null;
			}
			
			grid.isDragging = false;
			grid.hideDropFeedback.call(grid, e);
			
			if (this.accept == true)
			{
				if (grid.sheetobj)
				{
					if (this.nodeouttimer > -1)
					{
						clearTimeout(this.nodeouttimer);
					}
					
					this.nodeouttimer = setTimeout(function() {
						grid.hideDropFeedback.call(grid, e);
					}, 100);
					// grid.sheetobj.procUpdateReport.call(grid.sheetobj);
				}
			}
		},
		
		onNodeEnter : function(target, dd, e, data){
			grid.isDragging = true;
			
			var i,
				dt, dttype,
				accept = false;
				
			if (data.records && data.records.length > 0)
			{
				dt = data.records[0].data;
				dttype = dt.type.toLowerCase();
				
				accept = true;
			}
			
			if (accept == true)
			{
				grid.showDropHelper.call(grid, dt);
			}
			
			this.accept = accept;
		},
		onNodeOut : function(target, dd, e, data){
			
		},
		onNodeOver : function(target, dd, e, data){
			var dt,
				ret = (this.accept == true);
			if (this.accept == true)
			{
				if (data.records && data.records.length > 0)
				{
					dt = data.records[0].data;
				}
				else
				{
					dt = data.cellData;
				}
				
				grid.showDropFeedback.call(grid, e, dt);
			}
			return ret;
		},
		onNodeDrop : function(target, dd, e, data){
			grid.hideDropFeedback.call(grid, e);
			
			if (this.accept == true)
			{
				if (data.records && data.records.length > 0)
				{
					dt = data.records[0].data;
				}
				else
				{
					dt = data.cellData;
				}
				
				grid.onDropWidget.call(grid, e, dt);
			}
			
			this.accept = false;
			return true;
		}
	});
}