IG$.dlgDashboardVariable = $s.extend($s.window, {
	modal: true,
	closable: false,
	resizable:true,
	layout: "fit",
	autoHeight: true,
	width: 400,
	
	loadVariables: function() {
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(this, {
			title: IRm$.r1("T_DBD_VAR"),
			
			items: [
				{
					xtype: "panel",
					bodyPadding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "displayfield",
							value: "Variables"
						},
						{
							xtype: "gridpanel",
							flex: 1,
							minHeight: 400,
							store: {
								xtype: "store",
								fields: [
									"name", "uid", "nodepath", "type"
								]
							},
							columns: [
								{
									text: "",
									width: 25
								},
								{
									text: IRm$.r1("B_NAME"),
									flex: 1
								},
								{
									text: IRm$.r1("L_BIND_CONTROL"), // "Bind Control",
									flex: 1
								},
								{
									xtype: "actioncolumn",
									width: 50,
									items: [
										{
											iconCls: "icon-grid-delete",
											tooltip: "Delete variable",
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.getAt(rowIndex);
												grid.store.removeAt(rowIndex);
											}
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
				{
					text: IRm$.r1("B_CONFIRM"),
					handler:function() {
						this.close();
					},
					scope: this
				},
				{
					text: IRm$.r1("B_CLOSE"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui, opt) {
					var me = this;
					setTimeout(function() {
						me.loadVariables.call(me);
					}, 10);
				}
			}
		});
		
		IG$.dlgDashboardVariable.superclass.initComponent.apply(this, arguments);
	}
});