IG$.eventWizard = $s.extend($s.window, {
	modal: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:true,
	width: 500,
	height: 400,
	
	confirmSettings: function() {
		var me = this,
			mainpanel = me.mainpanel;
		
		if (mainpanel.updateSetting.call(mainpanel) == true)
		{
			me.callback && me.callback.execute();
			
			me.close();
		}
	},
	
	cancelAppendAction: function() {
		this.cardNav(0);
	},
	
	confirmAppendAction: function() {
		var me = this,
			actionname = me.down("[name=actionname]"),
			evdesc = actionname.getValue(),
			framecontent = me.framecontent,
			actionid = me.caction.name;
		
		if (evdesc == "")
		{
			return;
		}
		
		me.caction.desc = evdesc;
		framecontent.events[actionid] = me.caction;
		
		me.updateActionList();
		me.cardNav(0);
	},
	
	removeAction: function() {
		var me = this,
			actionname = me.down("[name=actionname]"),
			evdesc = actionname.getValue(),
			framecontent = me.framecontent,
			actionid = me.caction.name;
		
		if (evdesc == "")
		{
			return;
		}
		
		delete framecontent.events[actionid];
		
		me.updateActionList();
		
		me.caction = null;
		
		me.cardNav(0);
	},
	
	confirmMapping: function() {
		var me = this,
			controllist = me.down("[name=controllist]").store.data,
			i, 
			dt, framecontent = me.framecontent,
			ctrlname, ctrl, evname;
			
		for (i=0; i < controllist.items.length; i++)
		{
			dt = controllist.items[i].data;
			
			if (/(onloadevents|timerevents)/.test(dt.type) == true)
			{
				framecontent._applActions[dt.event] = dt.action || "";
			}
			else
			{
				ctrlname = dt.name;
				evname = dt.event;
				ctrl = framecontent.controls[ctrlname];
				
				ctrl.actionlist[evname] = dt.action || "";
			}
		}
		
		me.cardNav(0);
	},
	
	cancelMapping: function() {
		this.cardNav(0);
	},
	
	cardNav: function(indx) {
		var p = this.mainpanel,
			l = p.getLayout(); //this.getLayout(),
			// i = l.activeItem.id.split("card-")[1],
			//next = parseInt(i, 10) + incr;
			
		l.setActiveItem(indx);
	},
	
	goActionStep: function(step) {
		// step: 0 = guide
		// step: 1 = new action
		// step: 2 = modify action
		// step: 3 = map action
		
		var me = this;
		
		switch (step)
		{
		case 0:
			me.cardNav(0);
			break;
		case 1:
			var framecontent = me.framecontent,
				actionid;
			
			actionid = "action_" + framecontent.eventseq;
			framecontent.eventseq++;
			
			me.caction = {name: actionid, desc: "", actionlist: []};
			
			me.editActionList();
			break;
		case 2:
			// me.updateActionCombo();
			me.cardNav(2);
			break;
		}
	},
	
	editActionList: function() {
		var me = this,
			caction = me.caction,
			actionname = me.down("[name=actionname]");
						
		actionname.setValue(caction.desc || caction.name);
		
		me.validateActionEvent();
		
		me.cardNav(1);
	},
	
	initActionWizard: function() {
		var panel = this,
			wizard_step = $("#action_wizard_step"),
			step1 = $("<li class='m-link'><b><span>Create new action.</span></b></li>"),
			step2 = $("<li class='m-link'><span>Bind action to control.</span></li>")

		wizard_step.append(step1);
		wizard_step.append(step2);
				
		step1.bind("click", function() {
			panel.goActionStep.call(panel, 1);
		});
		
		step2.bind("click", function() {
			if (panel.framecontent.events)
			{
				var key,
					n = 0;
					
				for (key in panel.framecontent.events)
				{
					n++;
				}
				
				if (n == 0)
				{
					return;
				}
			}
			panel.goActionStep.call(panel, 2);
		});
		
		panel.updateActionList();
	},
	
	updateActionList: function() {
		var panel = this,
			key,
			framecontent = panel.framecontent,
			act,
			unode = $("#m-action-list");
		
		unode.empty();
		
		for (key in framecontent.events)
		{
			act = framecontent.events[key];
			
			var anode = $("<li id='" + key + "' class='m-link'><img src='./images/s.gif' class='item-icon icon-pkg'/><span>" + act.desc + "</span></li>");
			anode.bind("click", function() {
				var citem = this,
					cid = citem.id;
				
				if (panel.framecontent.events[cid])
				{
					panel.caction = panel.framecontent.events[cid];
					panel.editActionList.call(panel);
				}
			});
			anode.appendTo(unode);
		}
	},
	
	updateActionCombo: function() {
		var panel = this,
			key,
			act,
			dp = [];
			
		for (key in panel.framecontent.events)
		{
			act = panel.framecontent.events[key];
			
			dp.push({name: act.desc, value: key});
		} 
		
		panel._actioncombo.loadData(dp);
	},
	
	appendActionEvent: function() {
		var me = this,
			purpose = me.down("[name=purpose]").getValue();
		
		if (!purpose || purpose == "")
		{
			return;
		}
		
		var ev = new IG$.frameEvents(null);
		ev.type = purpose;
		ev.purpose = purpose;
		
		me.caction.actionlist.push(ev);
		
		me.setHandlerDetail(ev);
	},
	
	validateActionEvent: function() {
		var me = this,
			dp = [],
			i, j,
			item,
			evt,
			cactionlist = me.down("[name=cactionlist]"),
			caction = me.caction;
			
		for (i=0; i < caction.actionlist.length; i++)
		{
			evt = caction.actionlist[i];
			evt.uid = "action_" + i;
			item = {
				name: me.getActionLabel(evt.purpose), 
				type: evt.purpose,
				uid: evt.uid
			};
			
			switch (item.type)
			{
			case "onlinereport":
				item.desc = "";
				if (evt.item && evt.item.uid)
				{
					for (j=0; j < me.framecontent.dashboardreport.length; j++)
					{
						if (me.framecontent.dashboardreport[j].uid == evt.item.uid)
						{
							item.desc = me.framecontent.dashboardreport[j].name;
							break;
						}
					}
				}
				break;
			case "valuelist":
				item.desc = "";
				break;
			case "execute_script":
				item.desc = evt.script;
				break;
			case "servercall":
				item.desc = evt.jmethod + "(" + evt.jmodule_name + ")";
				break;
			}
			dp.push(item);
		}
		
		cactionlist.store.loadData(dp);
	},
	
	getActionLabel: function(purpose) {
		var me = this,
			i,
			r = "";
		
		for (i=0; i < me.eventdata.length; i++)
		{
			if (me.eventdata[i].purpose == purpose)
			{
				r = me.eventdata[i].desc;
				break;
			}
		}
		
		return r;
	},
	
	setHandlerDetail: function(ritem) {
		var me = this,
			dlg = null,
			framecontent = me.framecontent,
			uitem = null,
			i;
			
		for (i=0; i < me.caction.actionlist.length; i++)
		{
			if (me.caction.actionlist[i].uid == ritem.uid)
			{
				uitem = me.caction.actionlist[i];
				break;
			}
		}
		
		dlg = new IG$.actionWizard({
			framecontent: framecontent, 
			ritem: uitem,
			qmode: ritem.purpose,
			callback: new IG$.callBackObj(me, me.updateHandlerDetail)
		});
		
		IG$.checkLogin(me, dlg);
	},
	
	updateHandlerDetail: function() {
		this.validateActionEvent();
	},
	
	removeMappedEvent: function(ctrlname, evname) {
		var framecontent = this.framecontent,
			ctrl = framecontent.controls[ctrlname];
		
		if (ctrl)
		{
			ctrl.actionlist[evname] = null;
		}
	},
	
	initComponent : function() {
		var panel = this;
		
		panel.title = IRm$.r1('L_EVENT_WIZARD');
				
		panel.eventdata = IG$.eventdata;
		
		panel.mainpanel = new $s.panel({
			layout: "card",
			activeItem: 0,
			
			framecontent: panel.framecontent,
						
			init_f: function() {
				var me = this,
					j,
					framecontent = me.framecontent,
					dp = [],
					controllist = me.down("[name=controllist]"),
					actname,
					evtname,
					key;
				
				var onloadaction = {name: "OnLoad Action", type: "onloadevents", event: "onload", action: "", actiondesc: ""},
					timeraction = {name: "Timer Action", type: "timerevents", event: "ontimer", action: "", actiondesc: ""};
								
				actname = framecontent._applActions.onload;
				onloadaction.action = (framecontent.events[actname] && framecontent.events[actname] != "") ? actname : "";
				onloadaction.actiondesc = (framecontent.events[actname] && framecontent.events[actname] != "") ? framecontent.events[actname].desc : "";
				
				actname = framecontent._applActions.ontimer;
				timeraction.action = (framecontent.events[actname] && framecontent.events[actname] != "") ? actname : "";
				timeraction.actiondesc = (framecontent.events[actname] && framecontent.events[actname] != "") ? framecontent.events[actname].desc : "";

				dp.push(onloadaction);
				dp.push(timeraction);
								
				if (framecontent)
				{
					for (key in framecontent.controls)
					{
						var ctrl = framecontent.controls[key];
							
						if (ctrl && IG$.controls[ctrl.type] && IG$.controls[ctrl.type].p)
						{
							var p = IG$.controls[ctrl.type].p,
								dip = [];
							
							for (j=0; j < p.length; j++)
							{
								if (p[j].type == "event")
								{
									var lobj = {name: ctrl.name, type: ctrl.type, event: p[j].name, action: ""};
									actname = p[j].name;
									evtname = ctrl.actionlist[actname];
									if (evtname && framecontent.events[evtname])
									{
										lobj.action = evtname;
										lobj.actiondesc = framecontent.events[evtname].desc;
									}
									dip.push(lobj);
								}
							}
							
							for (j=0; j < dip.length; j++)
							{
								dp.push(dip[j]);
							}
						}
					}
				}
				
				controllist.store.loadData(dp);
			},
			
			updateSetting: function() {
				
				return true;
			},
			
			items: [
				{
					id: "card-0",
					xtype: "panel",
					border: false,
					autoLoad: {url: './html/action_wizard.html', callback: this.initActionWizard, scope: this},
					autoScroll: true
				},
				{
					id: "card-1",
					xtype: "form",
					plain: true,
					border: 0,
					bodyPadding: 5,
					fieldDefaults: {
						labelWidth: 100,
						anchor: '100%'
					},
					layout: {
						type: 'vbox',
						align: 'stretch'  // Child items are stretched to full width
					},
		
					items: [
						{
							xtype: "textfield",
							fieldLabel: IRm$.r1("L_ACTION_NAME"), // "Action name",
							name: "actionname"
						},
						{
							xtype: 'fieldcontainer',
							fieldLabel: IRm$.r1("L_ADD_ACTION"), // 'Add Action',
							labelStyle: 'font-weight:bold',
							layout: 'hbox',
							defaultType: 'textfield',
							items: [
								{
									xtype: "combobox",
									
									store: {
										xtype: "store",
										fields: [
											{name: 'purpose', mapping: "purpose", type: 'String'},
											{name: 'desc', mapping: "desc", type: 'String'}
										],
										data: this.eventdata
									},
									displayField: "desc",
									valueField: "purpose",
									editable: false,
									queryMode: "local",
									selectOnTab: false,
									name: "purpose"
								},
								{
									xtype: "button",
									text: IRm$.r1("B_APPEND"), // "Append",
									handler: function() {
										this.appendActionEvent();
									},
									scope: this
								}
							]
						},
						{
							xtype: "grid",
							height: 210,
							store: {
								xtype: "store",
								fields: [
									{name: 'name'},
									{name: 'type'},
									{name: 'uid'},
									{name: 'desc'},
									{name: 'delete'}
								]
							},
							name: "cactionlist",
							columns: [
								{
									text: IRm$.r1('B_NAME'),
									flex: 1,
									sortable: false,
									dataIndex: 'name'
								},
								{
									text: IRm$.r1('B_DESC'),
									flex: 1,
									sortable: false,
									dataIndex: 'desc'
								},
								{
									xtype: 'actioncolumn',
									width: 50,
									items: [
										{
											// icon: './images/brick.png',
											iconCls: "icon-grid-config",
											tooltip: 'Edit',
											handler: function (grid, rowIndex, colIndex) {
												var // rec = grid.store.getAt(rowIndex),
													ritem = panel.caction.actionlist[rowIndex];
												
												panel.setHandlerDetail.call(panel, ritem);
											}
										},
										{
											// icon: './images/delete.png',
											iconCls: "icon-grid-delete",
											tooltip: 'Delete item',
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.getAt(rowIndex);
												panel.caction.actionlist.splice(rowIndex, 1);
												grid.store.remove(rec);
											}
										}
									]
								}
							],
							viewConfig: {
								stripeRows: true
							},
							listeners: {
								itemdblclick: function(grid, record, item, index, e, eOpts) {
									var rec = grid.store.getAt(index),
										ritem = panel.caction.actionlist[index];
									
									panel.setHandlerDetail.call(panel, ritem);
								}
							}
						}
					],
					buttons:[
						{
							text: IRm$.r1("B_REMOVE"), // "Remove Action",
							handler: function() {
								this.removeAction();
							},
							scope: this
						}, "->",
						{
							text: IRm$.r1("B_CONFIRM"),
							handler: function() {
								this.confirmAppendAction();
							},
							scope: this
						}, 
						{
							text: IRm$.r1("B_CANCEL"),
							handler:function() {
								this.cancelAppendAction();
							},
							scope: this
						}
					]
				},
				{
					id: "card-2",
					xtype: "panel",
					layout: "fit",
					items: [
						{
							xtype: 'grid',
							store: {
								xtype: "store",
								fields: [
									{name: 'name'},
									{name: 'type'},
									{name: 'event'},
									{name: 'action'},
									{name: 'actiondesc'}
								]
							},
							name: 'controllist',
							stateful: true,
							height: 120,
							plugins: [
								{
									ptype: "cellediting",
									clicksToEdit: 1
								}
							],
							columns: [
								{
									text: IRm$.r1('B_NAME'),
									flex: 1,
									sortable: false,
									dataIndex: 'name',
									editable: false
								},
								{
									text: IRm$.r1('B_TYPE'),
									flex: 1,
									sortable: false,
									dataIndex: 'type',
									editable: false
								},
								{
									text: IRm$.r1('B_TYPE'),
									flex: 1,
									sortable: false,
									dataIndex: 'event',
									editable: false
								},
								{
									xtype: 'gridcolumn',
									width: 120,
									text: IRm$.r1("B_ACTION"),
									dataIndex: 'action',
									editable: true,
									editor: {
										xtype: 'combobox',
										queryMode: 'local',
										displayField: 'name',
										valueField: 'value',
										editable: false,
										autoSelect: true,
										triggerAction: 'all',
										selectOnTab: true,
										store: {
											xtype: "store",
											fields: [
												"name", "value"
											]
										},
										lazyRender: true,
										listClass: 'x-combo-list-small',
										listeners: {
											afterrender: function(view) {
												panel._actioncombo = view.store;
												panel.updateActionCombo.call(panel);
											}
										}
									}
								},
								{
									xtype: 'actioncolumn',
									width: 50,
									editable: false,
									items: [
										{
											// icon: './images/delete.png',
											iconCls: "icon-grid-delete",
											tooltip: 'Delete item',
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.getAt(rowIndex),
													ctrlname = rec.get("name"),
													evname = rec.get("event");
												
												panel.removeMappedEvent.call(panel, ctrlname, evname);
												rec.set("action", "");
											}
										}
									]
								}
							],
							viewConfig: {
								stripeRows: true
							}
						}
					],
					buttons: [
						{
							text: IRm$.r1("B_CONFIRM"),
							handler: function() {
								this.confirmMapping();
							},
							scope: this
						}, 
						{
							text: IRm$.r1("B_CANCEL"),
							handler:function() {
								this.cancelMapping();
							},
							scope: this
						}
					]
				},
				{
					id: "card-3",
					xtype: "panel",
					border: false,
					autoScroll: true,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					
					items: [
					]
				}
			],
			
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					
					panel.init_f();
				},
				
				cardswitch: function() {
					
				}
			}
		});
		
		$s.apply(this, {
			defaults:{bodyStyle:"padding:10px"},
			
			items: [
					this.mainpanel
				],
			buttons:[
				{
					id: "card-prev",
					text: IRm$.r1("L_GUIDE"), // "Guide",
					handler: function() {
						this.cardNav(0);
					},
					scope: this
				}, "->",
				{
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this.confirmSettings();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.eventWizard.superclass.initComponent.apply(this, arguments);
	}
});