IG$.dashboardPosition = function(panel, html) {
	var ctrl = this;
	
	ctrl.panel = panel;
	ctrl.html = html;
	
//	$(this.html).bind("resize", function() {
//		if (ctrl.t1 && ctrl.t2)
//		{
//			ctrl.validatePosition.call(ctrl);
//		}
//	});
}

IG$.dashboardPosition.prototype = {
	initDrawing: function() {
		var me = this,
			l = 5,
			main = $(me.html),
			html = $("<div></div>"),
			ttable = $("<table class='idv-dpm'><tr><td>Width</td><td class='mec-wfm'></td></tr><tr><td>Height</td><td class='mec-wfm'></td></tr></table>"),
			vtext = "<input type='checkbox' class='idv-poic'/>",
			itext = "<input type='text' class='idv-poi'/>",
			dv;
		
		me.hbox = $("<div></div>");
		
		// me.chelp = $("<div></div>");
			
		main.append(ttable);
		var t = ttable.find(".mec-wfm");
		
		html.css({position: "absolute", top: 40, right: 0, bottom: 0, left: 0});
		main.append(html);
		
		me.hbox.css({position: "absolute", background: "#cecece"});
		html.append(me.hbox);
		
		// me.chelp.css({position: "relative", background: "#ffffff", padding: 0, margin: 0, width: 100, height: 70, display: "none"});
		// me.hbox.append(me.chelp);
		
		dv = $("<table class='idv-dp-tb'><tr><td><input id='tw' type='checkbox' class='cbox'/><span>Width</span></td><td><input type='text' id='tw_value' class='idv-poiw' value=''></td></tr><tr><td><input id='th' type='checkbox' class='cbox'/><span>Height</span></td><td><input type='text' id='th_value' class='idv-poiw' value=''></td></tr></table>");
		main.prepend(dv);
		me.tw = $("#tw", dv);
		me.tw_value = $("#tw_value", dv);
		
		me.th = $("#th", dv);
		me.th_value = $("#th_value", dv);
		
		$.each(["tw", "th"], function(index, key) {
			me[key].bind("change", function() {
				var sender = this;
				me.checkChanged.call(me, sender);
			});
		});
		
		$.each(["tw_value", "th_value"], function(index, key) {
			me[key].bind("blur", function() {
				var sender = this;
				me.valueChanged.call(me, sender);
			});
			me[key].bind("keydown", function(ev) {
				if (ev.keyCode == 13)
				{
					var sender = this;
					me.valueChanged.call(me, sender);
				}
			});
		});
		
		$.each(["t1", "t2", "t3", "l1", "l2", "l3"], function(index, key) {
			me[key] = $(vtext, main);
			me[key][0].name = key;
			me[key + "_value"] = $(itext);
			me[key + "_value"][0].name = key + "_value";
			
			me[key].css({position: "absolute", top: l, left: l, display: (key == "t2" || key == "l2") ? "none" : "inline"});
			me[key + "_value"].css({position: "absolute", top: l, left: l});
			html.append(me[key]);
			html.append(me[key + "_value"]);
			
			me[key].bind("change", function() {
				var sender = this;
				
				me.checkChanged.call(me, sender);
			});
			
			me[key + "_value"].bind("blur", function() {
				var sender = this;
				
				me.valueChanged.call(me, sender);
			});
			
			me[key + "_value"].bind("keydown", function(ev) {
				if (ev.keyCode == 13)
				{
					var sender = this;
					me.valueChanged.call(me, sender);
				}
			});
		});
		
		me.validatePosition();
	},
	
	valueChanged: function(sender) {
		var me = this,
			sname = sender.name,
			pos = me.pos,
			tvalue = sender.value;
			
		switch (sname)
		{
		case "t1_value":
			pos.left = tvalue;
			break;
		case "t2_value":
			pos.center = tvalue;
			break;
		case "t3_value":
			pos.right = tvalue;
			break;
		case "l1_value":
			pos.top = tvalue;
			break;
		case "l2_value":
			pos.middle = tvalue;
			break;
		case "l3_value":
			pos.bottom = tvalue;
			break;
		case "tw_value":
			pos.width = tvalue;
			break;
		case "th_value":
			pos.height = tvalue;
			break;
		}
		
		me.updateValues();
	},
	
	updateValues: function() {
		var me = this;
		
		$.each([{l: "t1", v: "left"},
				{l: "t2", v: "center"},
				{l: "t3", v: "right"},
				{l: "l1", v: "top"},
				{l: "l2", v: "middle"},
				{l: "l3", v: "bottom"},
				{l: "tw", v: "width"},
				{l: "th", v: "height"}], 
			function(index, key) {
				if (me[key.l][0].checked == true)
				{
					var tvalue = me[key.l + "_value"][0].value,
						tkey = key.v;
					
					if (key.l == "tw" || key.l == "th")
					{
						if (tvalue.indexOf("%") > -1)
						{
							var tpercent = tvalue.split("%")[0];
							tvalue = tpercent;
							me.pos[tkey] = null;
							tkey = "percent" + tkey;
							tvalue = Number(tvalue);
							tvalue = Math.round(tvalue * 100) / 100;
							me.pos[tkey] = tvalue;
						}
						else
						{
							tvalue = Number(tvalue);
							tvalue = Math.round(tvalue * 100) / 100;
							me.pos[tkey] = tvalue;
							me.pos["percent" + tkey] = null;
						}
					}
					else
					{
						tvalue = Number(tvalue);
						tvalue = Math.round(tvalue * 100) / 100;
						me.pos[tkey] = tvalue;
					}
				}
			}
		);
		
		me.updatePreviewer();
		
		if (me.panel.positionchanged)
		{
			me.panel.positionchanged.call(me.panel);
		}
	},
	
	updateBoxValue: function(upos) {
		var me = this,
			pos = me.pos;
			
		var r = {x: 10, y: 10, width: null, height: null, percentwidth: null, percentheight: null, x1: 10, y1: 10};
		
		r.x = (pos.left != null) ? pos.left : 
			  (pos.right != null && pos.width != null) ? upos.width - pos.right - pos.width :
			  (pos.right != null && pos.percentwidth != null) ? upos.width - pos.right - (upos.width - pos.right) * 0.01 * pos.percentwidth :
			  -1;
			  
		r.y = (pos.top != null) ? pos.top : 
			  (pos.bottom != null && pos.height != null) ? upos.height - pos.bottom - pos.height :
			  (pos.bottom != null && pos.percentheight != null) ? upos.height - pos.bottom - (upos.height - pos.bottom) * 0.01 * pos.percentheight :
			  -1;
		
		if (pos.percentwidth != null)
		{
			r.percentwidth = pos.percentwidth;
			r.width = (pos.left != null) ? pos.percentwidth * 0.01 * (upos.width - pos.left) :
					  (pos.right != null) ? pos.percentwidth * 0.01 * (upos.width - pos.right) :
					  -1; 
		}
		else
		{
			r.width = (pos.width != null) ? pos.width : 
					  (pos.left != null && pos.right != null) ? upos.width - pos.left - pos.right : 
					  -1;
		}
		
		if (pos.percentheight != null)
		{
			r.percentheight = pos.percentheight;
			r.height = (pos.top != null) ? pos.percentheight * 0.01 * (upos.height - pos.top) :
					   (pos.bottom != null) ? pos.percentheight * 0.01 * (upos.height - pos.bottom) :
					   -1;
		}
		else
		{
			r.height = (pos.height != null) ? pos.height :
					   (pos.top != null && pos.bottom != null) ? upos.height - pos.bottom -pos.top :
					   -1;
		}
		
		r.width = (r.width != -1) ? Math.abs(r.width) : -1;
		r.height = (r.height != -1) ? Math.abs(r.height) : -1; 
		r.x = (r.x != -1) ? Math.abs(r.x) : -1;
		
		r.x1 = upos.width - (r.x + r.width);
		r.y1 = upos.height - (r.y + r.height);
		
		return r;
	},
	
	checkChanged: function(sender) {
		var me = this,
			sname = sender.name,
			checked = sender.checked,
			pos = me.pos,
			upos = me.upos,
			box;
			
		box = me.updateBoxValue(me.upos);
		
		switch (sname)
		{
		case "t1":
			if (checked == true)
			{
				pos.left = box.x;
//				if (pos.right != null && (pos.width != null || pos.percentwidth != null))
//				{
//					pos.right = null;
//				}
			}
			else
			{
				pos.left = null;
				if (pos.right == null)
				{
					pos.right = box.x1;
				}
				
				if (pos.width == null && pos.percentwidth == null)
				{
					pos.width = box.width;
				}
			}
			break;
		case "t2":
			break;
		case "t3":
			if (checked == true)
			{
				pos.right = box.x1;
//				if (pos.left != null && (pos.width != null || pos.percentwidth != null))
//				{
//					pos.left = null;
//				}
			}
			else
			{
				pos.right = null;
				if (pos.left == null)
				{
					pos.left = box.x;
				}
				
				if (pos.width == null && pos.percentwidth == null)
				{
					pos.width = box.width;
				}
			}
			break;
		case "l1":
			if (checked == true)
			{
				pos.top = box.y;
//				if (pos.bottom != null && (pos.height != null || pos.percentheight != null))
//				{
//					pos.bottom = null;
//				}
			}
			else
			{
				pos.top = null;
				if (pos.bottom == null)
				{
					pos.bottom = box.y1;
				}
				
				if (pos.height == null && pos.percentheight == null)
				{
					pos.height = box.height;
				}
			}
			break;
		case "l2":
			break;
		case "l3":
			if (checked == true)
			{
				pos.bottom = box.y1;
//				if (pos.top != null && (pos.height != null || pos.percentheight != null))
//				{
//					pos.top = null;
//				}
			}
			else
			{
				pos.bottom = null;
				if (pos.top == null)
				{
					pos.top = box.y;
				}
				if (pos.height == null && pos.percentheight == null)
				{
					pos.height = box.height;
				}
			}
			break;
		case "tw":
			if (checked == true)
			{
				pos.width = box.width;
				if (pos.left != null && pos.right != null)
				{
					pos.right = null;
				}
			}
			else
			{
				pos.width = null;
				pos.percentwidth = null;
				if (pos.left == null || pos.right == null)
				{
					pos.left = box.x;
					pos.right = box.x1;
				}
			}
			break;
		case "th":
			if (checked == true)
			{
				pos.height = box.height;
				
				if (pos.top != null && pos.bottom != null)
				{
					pos.bottom = null;
				}
			}
			else
			{
				pos.height = null;
				pos.percentheight = null;
				if (pos.top == null || pos.bottom == null)
				{
					pos.top = box.y;
					pos.bottom = box.y1;
				}
			}
			break;
		}
		
		me.setControlInput();
	},
	
	validatePosition: function() {
		var me = this,
			html = $(me.html),
			w = IG$.j$ext._w(html),
			h = IG$.j$ext._h(html) - 40,
			lw = IG$.j$ext._w($(me.t1)),
			lh = IG$.j$ext._h($(me.t1)) || 20,
			cw = IG$.j$ext._w($(me.t1_value)) || 60,
			ch = IG$.j$ext._h($(me.t1_value)) || 20,
			l = 10;
		
		me.hbox.css({left: lw+l, right: cw+l, top: lh+l, bottom: ch+l});
		
		var bbox = {x: lw+l, y: lh + l, w: IG$.j$ext._w(me.hbox), h: IG$.j$ext._h(me.hbox)};
		bbox.x1 = bbox.w + bbox.x;
		bbox.y1 = bbox.h + bbox.y;
		
		me.t1.css({left: bbox.x});
		me.t2.css({left: bbox.x + (bbox.w-lw) / 2});
		me.t3.css({left: bbox.x1 - lw});
		
		me.t1_value.css({top: bbox.y1, left: bbox.x});
		me.t2_value.css({top: bbox.y1, left: bbox.x + (bbox.w - cw) / 2});
		me.t3_value.css({top: bbox.y1, left: (w - cw - l)});
		
		me.l1.css({top: bbox.y});
		me.l2.css({top: bbox.y + (bbox.h - lh) / 2});
		me.l3.css({top: bbox.y1 - lh});
		
		me.l1_value.css({left: bbox.x1, top: bbox.y});
		me.l2_value.css({left: bbox.x1, top: bbox.y + (bbox.w - ch) / 2});
		me.l3_value.css({left: bbox.x1, top: bbox.y1 - ch});
	},
	
	setControlPosition: function(pos, upos) {
		var me = this;
		me.pos = pos;
		me.upos = upos;
		me.setControlInput();
	},
	
	setControlInput: function() {
		var me = this,
			pos = me.pos,
			upos = me.upos;
		
		pos.left = (typeof pos.left == 'undefined') ? null : pos.left;
		pos.right = (typeof pos.right == 'undefined') ? null : pos.right;
		pos.center = (typeof pos.center == 'undefined') ? null : pos.center;
		
		pos.top = (typeof pos.top == 'undefined') ? null : pos.top;
		pos.middle = (typeof pos.middle == 'undefined') ? null : pos.middle;
		pos.bottom = (typeof pos.bottom == 'undefined') ? null : pos.bottom;
		
		pos.width = (typeof pos.width == 'undefined') ? null : pos.width;
		pos.height = (typeof pos.height == 'undefined') ? null : pos.height;
		
		pos.percentwidth = (typeof pos.percentwidth == 'undefined') ? null : pos.percentwidth;
		pos.percentheight = (typeof pos.percentheight == 'undefined') ? null : pos.percentheight;
		
		if (pos.left == null && pos.right == null && pos.width == null)
		{
			pos.left = 10;
			pos.width = pos.percentwidth == null ? 50 : null;
		}
		
		if (pos.top == null && pos.bottom == null && pos.height == null)
		{
			pos.top = 10;
			pos.height = pos.percentheight == null ? 50 : null;
		}
		
		if (pos.left != null && pos.right != null)
		{
			pos.width = null;
			pos.percentwidth = null;
		}
		if (pos.top != null && pos.bottom != null)
		{
			pos.height = null;
			pos.percentheight = null;
		}
		
		if (pos.left != null && (pos.width != null || pos.percentwidth != null))
		{
			pos.right = null;
		}
		
		if (pos.right != null && (pos.width != null || pos.percentwidth != null))
		{
			pos.left = null;
		}
		
		if (pos.top != null && (pos.height != null || pos.percentheight != null))
		{
			pos.bottom = null;
		}
		
		if (pos.bottom != null && (pos.height != null || pos.percentheight != null))
		{
			pos.top = null;
		}
		
		me.updatePreviewer();
		
		me.setSelection("tw", (pos.width != null) ? pos.width : (pos.percentwidth != null) ? pos.percentwidth + "%" : -1);
		me.setSelection("th", (pos.height != null) ? pos.height : (pos.percentheight != null) ? pos.percentheight + "%" : -1);
		me.setSelection("t1", (pos.left != null) ? pos.left : -1);
		me.setSelection("t3", (pos.right != null) ? pos.right : -1);
		me.setSelection("l1", (pos.top != null) ? pos.top : -1);
		me.setSelection("l3", (pos.bottom != null) ? pos.bottom : -1);
	},
	
	updatePreviewer: function() {
		var me = this,
			w = IG$.j$ext._w(me.hbox),
			h = IG$.j$ext._h(me.hbox),
			bbox;
			
		bbox = me.updateBoxValue({width: w, height: h});
		
		// me.chelp.css({left: bbox.x, top: bbox.y, width: bbox.width, height: bbox.height});
		// me.chelp.show();
	},
	
	setSelection: function(ctrl, value) {
		var me = this;
		
		if (value == -1)
		{
			me[ctrl][0].checked = false;
			
			if (me[ctrl + "_value"])
			{
				me[ctrl + "_value"].css({display: "none"});
			}
		}
		else
		{
			me[ctrl][0].checked = true;
			if (me[ctrl + "_value"])
			{
				me[ctrl + "_value"].css({display: "inline"});
				me[ctrl + "_value"][0].value = value;
			}
		}
	}
}