IG$.dashboardFilter = $s.extend($s.window, {
	title: IRm$.r1('T_FILTER'),

	modal: true,

	layout: {
		type: 'fit',
		align: 'stretch'
	},
	
	closable: false,
	resizable:false,
	width: 400,
	height: 420,
	
	fitem: null,
	framecontent: null,
	
	callback: null,
	
	init_f: function() {
		var me = this,
			key,
			operatordp = [], opctrl = me.down("[name=operator]"),
			filterdim = me.down("[name=filterdim]"),
			ctrlvalue = me.down("[name=ctrlvalue]"),
			ctrldp = [],
			valuetype = me.down("[name=valuetype]"),
			valuedp,
			valuetext = me.down("[name=valuetext]");
			
		for (key in IG$.filteropcodes) {
			operatordp.push({value: key.name, label: key.locale});
		}
		
		opctrl.store.loadData(operatordp);
		
		for (key in me.framecontent.controls)
		{
			ctrldp.push({name: key, type: me.framecontent.controls[key].type});
		}
		
		ctrlvalue.store.loadData(ctrldp);
		
		valuedp = [
			{name: "control", value: "control"},
			{name: "value", value: "value"}
		];
		valuetype.store.loadData(valuedp);
			
		if (me.fitem)
		{
			filterdim.setValue(me.fitem.itemname || "");
			ctrlvalue.setValue(me.fitem.valuefield || "");
			valuetype.setValue(me.fitem.valuemode || "control");
			opctrl.setValue(me.fitem.operator || "IN");
			valuetext.setValue(me.fitem.valuetext || "");
		}
		
		me.validateLayout();
	},
	
	itemSelectedHandler: function(item) {
		var me = this,
			filterdim = me.down("[name=filterdim]"),
			fitem = me.fitem;
		
		fitem.nodepath = item.nodepath;
		fitem.itemuid = item.uid;
		fitem.itemtype = item.type;
		fitem.itemname = item.name;
		
		filterdim.setValue(fitem.itemname);
	},
	
	_confirm: function() {
		var me = this;
		if (me.updateDashboardFilter() == true)
		{
			me.callback && me.callback.execute(me.fitem);
			me.close();
		}
	},
	
	updateDashboardFilter: function() {
		var me = this,
			r = true;
			opctrl = me.down("[name=operator]"),
			filterdim = me.down("[name=filterdim]"),
			ctrlvalue = me.down("[name=ctrlvalue]"),
			valuetype = me.down("[name=valuetype]"),
			valuetext = me.down("[name=valuetext]"),
			fitem = me.fitem;
		
		if (fitem)
		{
			fitem.operator = opctrl.getValue();
			fitem.valuefield = ctrlvalue.getValue() || "";
			fitem.valuemode = valuetype.getValue() || "";
			fitem.valuetext = valuetext.getValue() || "";
		}
		
		return r;
	},
	
	validateLayout: function() {
		var me = this,
			filterdimpanel = me.down("[name=filterdimpanel]"),
			rbfiltertype = me.down("[name=rbfiltertype]").getGroupValue(),
			ctrlvalue = me.down("[name=ctrlvalue]"),
			valuetype = me.down("[name=valuetype]"),
			valuetext = me.down("[name=valuetext]"),
			operator = me.down("[name=operator]");
			
		switch (rbfiltertype)
		{
		case "metric":
			operator.show();
			filterdimpanel.show();
			valuetype.show();
			
			switch (valuetype.getValue())
			{
			case "control":
				valuetext.hide();
				ctrlvalue.show();
				break;
			case "value":
				valuetext.show();
				ctrlvalue.hide();
				break;
			}
			break;
		case "control":
			filterdimpanel.hide();
			operator.hide();
			valuetype.hide();
			ctrlvalue.show();
			break;
		}
		
	},
	
	initComponent : function() {
		var me = this,
			_vtstore,
			_opstore;
			
		_vtstore = {
			fields: [
				{name: 'name'},
				{name: 'value'}
			]
		};
		
		_opstore = {
			fields: [
				{name: 'name'},
				{name: 'label'},
				{name: 'value'}
			]
		};
		
		_ctstore = {
			fields: [
				{name: 'name'},
				{name: 'type'}
			]
		};
		
		var mainpanel = new $s.formpanel({
			xtype: "form",
			
			plain: true,
			border: 0,
			bodyPadding: 5,
			
			fieldDefaults: {
				labelWidth: 100,
				anchor: '100%'
			},
			
			layout: {
				type: 'vbox',
				align: 'stretch'  // Child items are stretched to full width
			},
			
			items: [
				{
					xtype: "radiogroup",
					vertical: false,
					fieldLabel: IRm$.r1("L_FILTER_TYPE"), // "Filter Type",
					name: "filtertype",
					plain: true,
					items: [
						{
							boxLabel: 'Select item', 
							name: 'rbfiltertype', 
							inputValue: "metric", 
							checked: true
						},
						{
							boxLabel: 'Control', 
							name: 'rbfiltertype', 
							inputValue: "control"
						}
					],
					listeners: {
						change: function(field, newvalue, oldvalue, eOpts) {
							this.validateLayout();
						},
						scope: this
					}
				},
				{
					xtype: 'fieldcontainer',
					name: "filterdimpanel",
					fieldLabel: IRm$.r1('L_ITEM'),
					combineErrors: false,
					layout: 'hbox',
					defaults: {
						hideLabel: true
					},
					items: [
						{
							xtype: "textfield",
							name: "filterdim",
							emptyText: IRm$.r1("M_SEL_ITEM"),
							readOnly: true
						},
						{
							xtype: 'button',
							// text: "..",
							iconCls: "icon-toolbar-search",
							tooltip: IRm$.r1("B_SEARCH"),
							width: 20,
							handler: function() {
								var me = this,
									cubeuid = me.fitem.cubeuid;
								
								if (!cubeuid)
									return;
									
								var dlgitemsel = new IG$.metaSelectDlg({
									visibleItems: 'folder;metric;measure',
									tree_options: {
										cubebrowse: true,
										rootuid: cubeuid
									}
								});
								dlgitemsel.callback = new IG$.callBackObj(me, me.itemSelectedHandler);
								IG$.checkLogin(me, dlgitemsel);
							},
							scope: this
						}
					]
				},
				{
					xtype: "combo",
					
					fieldLabel: IRm$.r1("B_OP"), // "Operator",
					name: "operator",
					
					store: _opstore,
					displayField: "label",
					valueField: "value",
					
					editable: false,
					queryMode: "local",
					selectOnTab: false
				},
				{
					xtype: "combo",
					
					fieldLabel: IRm$.r1("L_VALUE_TYPE"), // "Value type",
					name: "valuetype",
					
					store: _vtstore,
					
					displayField: "name",
					valueField: "value",
					
					editable: false,
					queryMode: "local",
					selectOnTab: false,
					
					listeners: {
						select: function() {
							this.validateLayout();
						},
						scope: this
					}
				},
				{
					xtype: "textfield",
					fieldLabel: IRm$.r1("L_VALUE_TEXT"), // "Value text",
					name: "valuetext"
				},
				{
					xtype: "combo",
					fieldLabel: IRm$.r1("L_CONTROL_VALUE"), // "Control value",
					name: "ctrlvalue",
					
					store: _ctstore,
					
					displayField: "name",
					valueField: "name",
					
					editable: false,
					queryMode: "local",
					selectOnTab: false
				}
			],
			listeners: {
				afterrender: function(ui) {
					var panel = this;
					panel.init_f();
				},
				scope: this
			}
		});
				 
		$s.apply(this, {
			
			defaults:{bodyStyle:'padding:10px'},
			
			items: [
				mainpanel
			],
			
			buttons:[
				'->',
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.dashboardFilter.superclass.initComponent.apply(this, arguments);
	}
});