IG$.columnstore = $s.extend($s.window, {

	modal: false,
	isWindow: true,
	region:"center",
	layout: {
		type: "fit",
		align: "stretch"
	},
	
	closable: false,
	resizable:false,
	width: 560,
	height: 400,
	
	framecontent: null,
	
	callback: null,
	
	_confirm: function() {
		var me = this,
			r = true,
			fctrl = me.fctrl,
			i,
			rec,
			g_store = me.down("[name=g_store]");
			
		if (fctrl)
		{
			fctrl._f_columns = [];
			
			for (i=0; i < g_store.store.data.items.length; i++)
			{
				rec = g_store.store.data.items[i];
				fctrl._f_columns.push({
					name: rec.get("name"),
					title: rec.get("title"),
					hidden: rec.get("hidden"),
					sortable: rec.get("sortable"),
					kname: rec.get("kname"),
					cstyle: rec.get("cstyle"),
					columnwidth: rec.get("columnwidth")
				});
			}
		}
			
		if (r == true)
		{
			me.callback && me.callback.execute();
			
			me.close();
		}
	},
	
	initF: function() {
		var me = this,
			framecontent = me.framecontent,
			events = framecontent.events,
			k,
			dp = [],
			g_store = me.down("[name=g_store]");
			
		g_store.store.loadData(me.fctrl._f_columns);
			
		$.each(events, function(k, val) {
			var i,
				action;
			
			for (i=0; i < val.actionlist.length; i++)
			{
				if (val.actionlist[i].purpose == "sql")
				{
					action = val.actionlist[i];
					dp.push({
						actiongroup: k,
						purpose: action.purpose,
						seq: action.seq,
						uid: action.uid,
						name: action.name,
						description: action.description
					});
				}
			}
		});
		
		me.down("[name=g_actions]").store.loadData(dp);
	},
	
	importColumn: function(rec) {
		var me = this,
			framecontent = me.framecontent,
			events = framecontent.events,
			action,
			agroup,
			i,
			dp;
			
		agroup = events[rec.get("actiongroup")];
		
		if (agroup)
		{
			for (i=0; i < agroup.actionlist.length; i++)
			{
				if (agroup.actionlist[i].seq == rec.get("seq"))
				{
					action = agroup.actionlist[i];
				}
			}
		}
		
		if (action)
		{
			dp = [];
			
			if (action.purpose == "sql" && action.columns)
			{
				for (i=0; i < action.columns.length; i++)
				{
					dp.push({
						name: action.columns[i].name,
						kname: action.columns[i].kname
					});
				}
			}
			
			me.down("[name=g_store]").store.loadData(dp);
		}
	},
	
	initComponent : function() {
		var me = this;
		
		me.title = "Grid Store Configuration",
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "card",
					name: "mcard",
					bodyPadding: 10,
					items: [
						{
							xtype: "gridpanel",
							name: "g_store",
							flex: 1,
							selType: "checkboxmodel",
							selModel: {
								checkSelector: ".x-grid-cell",
								mode: "MULTI"
							},
							store: {
								xtype: "store",
								fields: [
									"name", "title", "hidden", "sortable", "kname", "cstyle", "columnwidth"
								]
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									dataIndex: "name",
									flex: 1,
									minWidth: 120
								},
								{
									text: IRm$.r1("B_TITLE"),
									dataIndex: "title",
									flex: 1,
									minWidth: 120
								},
								{
									text: IRm$.r1("B_HIDDEN"),
									xtype: "checkcolumn",
									width: 60,
									dataIndex: "hidden"
								},
								{
									text: IRm$.r1("L_SORTABLE"), // "Sortable",
									xtype: "checkcolumn",
									width: 60,
									dataIndex: "sortable"
								},
								{
									text: IRm$.r1("L_COLUMN_STYLE"), // "Column Style",
									flex: 1,
									minWidth: 120,
									dataIndex: "cstyle"
								},
								{
									text: IRm$.r1("L_COLUMN_WIDTH"), // "Column Width",
									width: 80,
									dataIndex: "columnwidth"
								}
							],
							tbar: [
								{
									text: IRm$.r1("B_MOVE_UP"), // "Move Up",
									handler: function() {
									},
									scope: this
								},
								{
									text: IRm$.r1("B_MOVE_DOWN"), // "Move Down",
									handler: function() {
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1("L_IMP_FROM_ACTION"), // "Import from Action",
									handler: function() {
										this.down("[name=mcard]").getLayout().setActiveItem(1);
									},
									scope: this
								}
							]
						},
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "gridpanel",
									name: "g_actions",
									flex: 1,
									selType: "checkboxmodel",
									selModel: {
										checkSelector: ".x-grid-cell",
										mode: "SINGLE"
									},
									store: {
										xtype: "store",
										fields: [
											"actiongroup", "purpose", "eventtype", "uid", "name", "description", "seq"
										]
									},
									columns: [
										{
											text: IRm$.r1("B_GROUP"), // "Group",
											dataIndex: "actiongroup"
										},
										{
											text: IRm$.r1("B_TYPE"),
											dataIndex: "eventtype"
										},
										{
											text: IRm$.r1("B_NAME"),
											dataIndex: "name"
										},
										{
											text: IRm$.r1("B_DESC"),
											dataIndex: "description",
											flex: 1
										}
									]
								},
								{
									xtype: "toolbar",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										"->",
										{
											text: IRm$.r1("L_IMP_COLUMNS"), // "Import Columns",
											handler: function() {
												var me = this,
													sel = me.down("[name=g_actions]").getSelectionModel().selected,
													rec;
												if (sel && sel.length > 0)
												{
													rec = sel.items[0];
													me.importColumn(rec);
												}
												me.down("[name=mcard]").getLayout().setActiveItem(0);
											},
											scope: this
										}
									]
								}
							]
						}
					]
				}
			],
			
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			
			listeners: {
				afterrender: function(tobj) {
					me.initF.call(me);
				}
			}
		});
		
		IG$.columnstore.superclass.initComponent.apply(this, arguments);
	}
});
