IG$.d$ScrWiz = $s.extend($s.panel, {

	modal: true,

	layout: {
		type: "fit",
		align: "stretch"
	},
	
	
	framecontent: null,
	
	callback: null,
	
	confirmSettings: function() {
		if (this.updateSetting() == true)
		{
			this.callback && this.callback.execute();
			
			this.close();
		}
	},
	
	initF: function() {
		var me = this,
			scriptctrl = me.down("[name=script]"),
			scriptdoc = $(".idv-jcl-edtr", scriptctrl.el.dom),
			editor;
			
		scriptdoc.empty();
		
		w = IG$.j$ext._w(scriptdoc);
		h = IG$.j$ext._h(scriptdoc);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(scriptdoc);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		if (!window.ace)
		{
			IG$.getScriptCache(ig$.bootconfig.scmap.igcg, new IG$.callBackObj(me, function() {
				var me = this,
					editor;
				
				editor = ace.edit(jview[0]);
				editor.getSession().setMode("ace/mode/java");
				
				me.scripteditor = editor;
				me.scriptview = jview;
			}));
		}
		else
		{
			editor = ace.edit(jview[0]);
			editor.getSession().setMode("ace/mode/java");
			
			me.scripteditor = editor;
			me.scriptview = jview;
		}
	},
	
	commitCode: function() {
		var me = this,
			r;
		
		r = me.updateSetting();
		
		return r;
	},
	
	refreshCode: function() {
		var me = this,
			scripteditor = me.scripteditor,
			script;
			
		script = Base64.decode(this.framecontent.activescript);
		scripteditor.setValue(script);
	},
	
	init_f: function() {
		var me = this,
			// controls = this.framecontent.controls,
			// ctrllist = this.down("[name=ctrllist]"),
			editor = me.scripteditor;
			// dp = [], w, h, jview;
		
//		$.each(controls, function(index, key) {
//			var ctrl = controls[index];
//			
//			dp.push({name: ctrl.name, type: ctrl.type});
//		});
		
		var script = Base64.decode(this.framecontent.activescript);
		editor.setValue(script);
		// ctrllist.store.loadData(dp);
	},
	
	updateSetting: function() {
		var me = this,
			scriptdoc = me.scripteditor,
			script = Base64.encode(scriptdoc.getValue()),
			framecontent = me.framecontent;
		
		framecontent.activescript = script;
		framecontent.parseScriptFunc.call(framecontent);
		
		return true;
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(this, {
			// title: IRm$.r1('L_SCRIPT_WIZARD'),
			
			items: [
				{
					xtype: "panel",
					layout: "border",
				
					items: [
						{
							xtype: "panel",
							region: "center",
							layout: "fit",
							title: "Script content",
							border: 0,
							items: [
								{
									name: "script",
									html: "<div class='idv-jcl-edtr'></div>",
									listeners: {
										afterrender: function(tobj) {
											me.initF.call(me);
										},
										resize: function(tobj, w, h, ow, oh, opts) {
											if (me.scriptview && me.scripteditor)
											{
												IG$.j$ext._w(me.scriptview, w);
												IG$.j$ext._h(me.scriptview, h);
												me.scripteditor.resize(true);
											}
										}
									}
								}
							]
						}
					]
				}
			]
		});
		
		IG$.d$ScrWiz.superclass.initComponent.apply(this, arguments);
	}
});


IG$.d$ViewSrc = $s.extend($s.panel, {

	modal: true,

	layout: {
		type: "fit",
		align: "stretch"
	},
	
	
	framecontent: null,
	
	callback: null,
	
	confirmSettings: function() {
		if (this.updateSetting() == true)
		{
			this.callback && this.callback.execute();
			
			this.close();
		}
	},
	
	commitCode: function() {
		var r = this.updateSetting();
		
		return r;
	},
	
	refreshCode: function() {
		var me = this,
			layout,
			editor = me.scripteditor,
			lstr;
		
		layout = me.framecontent.getLayout();
		lstr = JSON.stringify(layout, null, 4);
		editor.setValue(lstr);
	},
	
	initF: function() {
		var me = this,
			scriptctrl = me.down("[name=script]"),
			scriptdoc = $(".idv-jcl-edtr", scriptctrl.el.dom),
			editor;
			
		scriptdoc.empty();
		
		w = IG$.j$ext._w(scriptdoc);
		h = IG$.j$ext._h(scriptdoc);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(scriptdoc);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		editor = ace.edit(jview[0]);
		editor.setTheme("ace/theme/textmate");
		editor.getSession().setMode("ace/mode/java");
		
		this.scripteditor = editor;
		this.scriptview = jview;
	},
	
	init_f: function() {
		var me = this,
			editor = me.scripteditor,
			layout,
			lstr;
		
		layout = this.framecontent.getLayout();
		lstr = JSON.stringify(layout);
		editor.setValue(lstr);
	},
	
	updateSetting: function() {
		var me = this,
			scriptdoc = me.scripteditor,
			script = scriptdoc.getValue();
		
		r = me.framecontent.setLayout(script);
		
		return r;
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "border",
				
					items: [
						{
							xtype: "panel",
							region: "center",
							layout: "fit",
							border: 0,
							items: [
								{
									name: "script",
									html: "<div class='idv-jcl-edtr'></div>",
									listeners: {
										afterrender: function(tobj) {
											me.initF.call(me);
										},
										resize: function(tobj, w, h, ow, oh, opts) {
											if (me.scriptview && me.scripteditor)
											{
												IG$.j$ext._w(me.scriptview, w);
												IG$.j$ext._h(me.scriptview, h);
												me.scripteditor.resize(true);
											}
										}
									}
								}
							]
						}
					]
				}
			]
		});
		
		IG$.d$ViewSrc.superclass.initComponent.apply(this, arguments);
	}
});	